.boardWrapper {
    display: grid;
    /* grid-template-columns: repeat(5, 1fr); */
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 1rem;
    margin: 1rem;
}

.card {
    padding: 20px;
    border: 1px solid black;
    background-color: floralwhite;
    font-weight: bolder;
}

.red {
    color: red;
}

.blue {
    color: blue;
}

.gray {
    color: rosybrown;
}

.black {
    color: black;
}




